/* LOADER */

.loaderLight {
  letter-spacing: .5vh;
  background: -webkit-linear-gradient(left, #C0C0C0 0%,#D3D3D3 24%,#D3D3D3 36%,#C0C0C0 46%,#C0C0C0 52%,#C0C0C0 58%,#C0C0C0 67%,#000000 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 300%;
  transition: 3s;
  -webkit-animation: font-shine 3s infinite linear;
          animation: font-shine 3s infinite linear;
}

@-webkit-keyframes font-shine {
  0% {background-position: 0% 0}
  100% {background-position: -100% 0}
}

@keyframes font-shine {
  0% {background-position: 0% 0}
  100% {background-position: -100% 0}
}


